<template>
    <div class = "chat">
        <div class = "username">{{friendname}}</div>
        <div class = "allmsg">
            <div class = "message" :v-show="msgs === []">
                <div class = "onemsg">
                    现在还没有消息捏
                </div>
            </div>
            <div class = "message" v-for="msg in msgs" :key = "msg.id">
                <div class = "onemsg">
                    {{msg.name}}: <span :style="'background-color:' + (msg.color ? '#ffa1d0' : '#fff2f9') ">{{msg.content}}</span>
                </div>
            </div>
        </div>
        
        <textarea  type = "text" class = "inputmsg" placeholder = "这里输入消息" @keyup.enter = "add" v-model="str"></textarea>
    </div>
</template>

<script>
// var receive
export default {
    data()
    {
        return {
            msgs : localStorage.getItem("msgs")|| [],
            str : "",
            username : "",
            friendname : "",
            userid: "",
            ws : null
        }
    },
    methods :
    {
        add()
        {
            if(this.str == "") return;
            this.msgs.push({id : Date.now(), content : this.str, name : this.username, color : 1});
            console.log(this.str);
            this.ws.send(this.str);
            this.str = "";
        },
        getCookie(str)
        {
            console.log("cookie is ",window.document.cookie);
            let cookies = window.document.cookie.split(';');
            console.log(cookies);
            let aim = str + "="
            for(let i = 0; i < cookies.length;i++)
            {
                let tmp = cookies[i].trim();
                if(tmp.indexOf(aim) == 0)
                {
                    return tmp.substring(aim.length, tmp.length);
                }
            }
        }
    },
    mounted()
    {
        this.friendname = this.getCookie("friendname");
        this.username = this.getCookie("username");
        this.userid = this.getCookie("userid");
        this.ws = new WebSocket("ws://127.0.0.1:8080/startchat?userid=" + this.userid);
        this.ws.addEventListener("message", (event) => {
            this.msgs.push({id : Date.now(), content : event.data, name : this.friendname, color : 0});
        })  
    },
    beforeDestroy()
    {
        this.ws.Close();
    }
}
</script>

<style scoped>

.chat {
    margin: auto auto auto auto;
    width: 1200px;
    height: 100%;
    border: 1px solid #bfbfbf;
    transition: all 0.5s ease 0s;
}

.chat:hover {
    box-shadow: 0 0 3px 0px;
}

.username {
    font-size: 20px;
    padding-bottom: 10px;
    padding-top: 10px;
    text-align: center;
    color: #212121;
    border-bottom: 1px solid #bfbfbf;
    background-color: #ffe3f0;
}

.username:hover {
    color: #ff409f;
}

.allmsg {
    width: auto;
    height: 100%;
}

.message {
    width: auto;
    height: auto;
    background-color: #fffafc;
}
.onemsg {
    display: block;
    height: 30px;
    margin: 0px 20px;
    padding: 20px 10px;
    /* background-color: #ffa1d0; */
}

.onemsg span {
    background-color: #ffa1d0;
    padding: 5px;
    border-radius: 5px;
}

.inputmsg {
    display: block;
    width: 1190px;
    height: 123px;
    border-style: none;
    resize: none;
    outline: none;
    padding: 5px;
    background-color: #fff2f9;
}

</style>